// Extra Large = 1440px
@mixin extraLarge {
  @media (max-width: ($width__extraLarge - 1)) {
    @content;
  }
}

@mixin minExtraLarge {
  @media (min-width: ($width__extraLarge)) {
    @content;
  }
}

