@function rem($value) {
  $remValue: calc($value / 16) + rem;
  @return $remValue;
}



@mixin buttonColor($color) {
  .button {
    &--#{$color} {
      color: $color;
      border: 1px solid white;

      .button__text {
        color: color;
      }

      .button__arrow {
        path {
          stroke: color;
        }
      }
    }
  }
}

@mixin resetSummary {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: rem(28);

  > * {
    color: $color_main__text;
    text-decoration: none;
    font-weight: normal;
  }
}
@mixin resetSummarySmall {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: rem(25);

  > * {
    color: $color_main__text;
    text-decoration: none;
    font-weight: normal;
  }
}